@use 'base/variables' as v;
@use 'base/mixins' as m;

.olvide {
    margin-top: 5rem;

    .uptask {
        @include m.textoDegradado(v.$rosa);
    }

    input[type='submit'] {
        @include m.boton(v.$rosa, v.$blanco);
        border-radius: 1rem;
    }

    .acciones {
        a {
            color: v.$rosa;
            font-weight: bold;
        }
    }
}
